import React, { Component } from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import ImageMapEditor from "./views/EditorLib/components/imagemap/ImageMapEditor";
// import { renderRoutes } from 'react-router-config';
import "./App.scss";

const loading = () => (
  <div className="animated fadeIn pt-3 text-center">Loading...</div>
);

// Containers
const DefaultLayout = React.lazy(() =>
  import("./views/Containers/DefaultLayout/DefaultLayout")
);

// Pages
const Login = React.lazy(() => import("./views/CommonPages/Login"));
const Register = React.lazy(() => import("./views/CommonPages/Register"));
const Page404 = React.lazy(() => import("./views/CommonPages/Page404"));
const Page500 = React.lazy(() => import("./views/CommonPages/Page500"));

class App extends Component {
  render() {
    return (
      <HashRouter>
        <React.Suspense fallback={loading()}>
          <Switch>
            <Route
              path="/template"
              name="Editor"
              render={props => (
                <div className="rde-main">
                  <div className="rde-content">
                    <ImageMapEditor {...props} />
                  </div>
                </div>
              )}
            />
            <Route
              exact
              path="/login"
              name="Login Page"
              render={props => <Login {...props} />}
            />
            <Route
              exact
              path="/register"
              name="Register Page"
              render={props => <Register {...props} />}
            />
            <Route
              exact
              path="/404"
              name="Page 404"
              render={props => <Page404 {...props} />}
            />
            <Route
              exact
              path="/500"
              name="Page 500"
              render={props => <Page500 {...props} />}
            />
            <Route
              path="/"
              name="Home"
              render={props => <DefaultLayout {...props} />}
            />
          </Switch>
        </React.Suspense>
      </HashRouter>
    );
  }
}

export default App;
